.TodoInsert {
	display: flex;
	background-color: #495057;

	input {
		flex: 1;
		background: none;
		outline: none;
		border: none;
		padding: 0.5rem;
		font-size:1.125rem;
		line-height: 1.5;
		color: #fff;

		&::placeholder {
			color: #dee2e6;
		}
	}

	button {
		background: none;
		outline: none;
		border: none;
		background-color: #868e96;
		color: #fff;
		padding: 0 1rem;
		font-size: 1.5rem;
		display: flex;
		align-items: center;
		cursor: pointer;
		transition: 0.1s background ease-in;
		&:hover {
			background-color: #adb5bd;
		}
	}
}