.TodoTemplate {
	width: 512px;
	margin: 6rem auto;
	border-radius: 4px;
	overflow: hidden;

	.app-title {
		background-color: #22b8cf;
		color: #fff;
		height: 4rem;
		font-size: 1.5rem;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.content {
		background-color: #fff;
	}
}