.TodoListItem-virtualized {
	& + & {
		border-top: 1px solid #dee2e6;
	}
	&:nth-child(even) {
		background-color: #f8f9fa;
	}
	
}

.TodoListItem {
	padding: 1rem;
	display: flex;
	align-items: center;

	// &:nth-child(even) {
	// 	background-color: #f8f9fa;
	// }

	.checkbox {
		cursor: pointer;
		flex: 1;
		display: flex;
		align-items: center;

		svg {
			font-size: 1.5rem;
		}

		.text {
			margin-left: 0.5rem;
			flex: 1;
		}

		&.checked {
			svg {
				color: #22b8cf;
			}
			.text {
				color: #adb5db;
				text-decoration: line-through;
			}
		}
	}

	.remove {
		display: flex;
		align-items: center;
		font-size: 1.5rem;
		color: #ff6b6b;
		cursor: pointer;
		&:hover {
			color: #ff8787;
		}
	}

	// & + & {
	// 	border-top: 1px solid #dee2e6;
	// }
}